@import 'lib/panel';

.content {
  @extend %panel;
  
  float: left;
  width: calc(100% - 218px);
  margin-left: 16px;

  &.editing {
    box-shadow: inset 0px 0px 30px -10px rgba(0, 0, 0, .5);
  }

  .content-body {
    padding: 32px;
  }

  .content-titlebar {
    background: $grey-lighter;
    padding: 12px 16px;
    border-top-left-radius: $border-radius;
    border-top-right-radius: $border-radius;

    .content-titlebar-title {
      display: inline-block;
      font-weight: 700;
    }

    .content-titlebar-controls {
      display: inline-block;
      text-align: right;

      a {
        margin: 12px 0 12px 12px;
      }
    }
  }
}