.side-nav {
  width: 200px;
  float: left;

  .add-page-button {
    background: lighten($red, 20%);
    color: white;
    width: 100%;
    padding: 12px;
    border-radius: $border-radius;
    font-weight: 600;
    cursor: pointer;
    margin: 24px 0;
    border: none;

    &:hover {
      background: $red;
    }

    .icon {
      font-weight: bold;
    }

    .icon, .button-content {
      display: inline-block;
      font-size: $font-size;
      line-height: 16px;
      vertical-align: middle;

      &:not(:last-child) {
        margin-right: 4px;
      }
    }
  }
  
  .nav-heading {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    margin-left: 8px;
    padding: 8px 0;
  }

  ul {
    list-style-type: none;
    padding: 0;
    margin-top: 0;
    margin-bottom: 24px;
  }

  li {
    font-size: 14px;
    
    a {
      display: block;
      padding: 8px 16px;
      border-radius: 2px;
      text-decoration: none;
      color: $text-color;
    }
    
    a.active {
      background: white;
      padding-left: 12px;
      border-left: 4px solid $red;
    }

    a.add-page-button {
      color: $text-color-lighter;
      cursor: pointer;
    }
  }
}