.navbar {
  background: white;
  position: absolute;
  width: 100%;
  border-bottom: 1px solid #ccc;
  top: 0;
  border-top: 4px solid $red;
}

.site-title {
  font-family: Merriweather, serif;
  font-weight: bold;
  color: $red;
  font-size: 18px;
  float: left;
  line-height: 60px;
  vertical-align: middle;
  text-decoration: none;
}

.header-right {
  float: right;

  .header-button {
    display: inline-block;
    color: $text-color-lighter;
    padding: 12px;
    margin: 8px 0 8px 8px;
    vertical-align: middle;
    text-decoration: none;
    font-weight: 700;
    border-radius: $border-radius;
    cursor: pointer;
    transition: 0.35s;

    &:hover {
      color: $text-color;
      background: $grey-light-2;
    }
  }
}