.text-editor-container {
  .text-editor {
    box-shadow: inset 0px 0px 30px -10px rgba(0, 0, 0, .5);
    padding: 16px 32px;
  }

  .text-editor-toolbar {
    position: relative;
    padding: 6px 12px;
    border-bottom: 2px solid #eee;

    .text-editor-button {
      cursor: pointer;
      color: $grey;
      display: inline-block;
      padding: 6px;
      line-height: 20px;
      vertical-align: middle;
    
      &.active {
        color: black;
      }

      .text-editor-icon {
        font-size: 20px;
      }
    }
  
    .text-editor-toolbar-seperator {
      display: inline-block;
      height: 32px;
      width: 32px;
    }

    .text-editor-toolbar-actions {
      float: right;

      .text-editor-action {
        cursor: pointer;
        margin: 9px 4px;
        background: lighten($grey-light, 10%);
        border-radius: $border-radius;
        padding: 6px 16px;
        color: darken($grey, 10%);
        font-weight: 700;
        border: none;
        font-size: 14px;
        
        // If the button is a link
        text-decoration: none;
  
        &:hover, &:active {
          background: $grey-light;
          color: $text-color;
        }
  
        &.primary {
          background: lighten($red, 20%);
          color: white;

          &:hover, &:active {
            background: $red;
          }
        }
      }
    }
  }
}