$modal-border-radius: 4px;

.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: hsla(221, 100%, 0%, .8);


  .modal {
    @extend %panel;
    background: white;
    border-radius: $modal-border-radius;
    width: 500px;
    position: absolute;
    transform: translateX(-50%) translateY(-50%);
    top: 30%;
    left: 50%;
    border: 1px;

    .modal-header {
      border-top-left-radius: $modal-border-radius;
      border-top-right-radius: $modal-border-radius;
      background: $grey-lighter;
      padding: 16px;
      font-weight: bold;
      font-size: 16px;
    }

    .modal-body {
      padding: 16px;
    }
  }
}