$success-background: lighten(desaturate($green-light, 60%), 47%);
$success-border-color: darken($success-background, 13%);
$success-color: darken($green, 4%);

$error-background: lighten(desaturate($red, 20%), 43%);
$error-border-color: darken($error-background, 10%);
$error-color: darken($red, 10%);

.notifications {
  position: fixed;
  bottom: 16px;
  right: 16px;
  z-index: 1;
  width: 400px;
  max-width:90%;

  .notification {
    background: white;
    box-shadow: 1px 1px 8px -3px $grey-light;
    border-radius: $border-radius;
    padding: 16px 24px;
    border: 1px solid;
    margin-top: 16px;
  }

  .notification-success {
    background: $success-background;
    border-color: $success-border-color;
    color: $success-color;
  }

  .notification-error {
    background: $error-background;
    border-color: $error-border-color;
    color: $error-color;
  }
}