 // Variables
 @import 'variables';

// Misc
@import 'navbar';
@import 'side-nav';
@import 'typography';
@import 'text-editor';
@import 'content';
@import 'notifications';
@import 'auth';
@import 'modal';

/**
 * Misc styles
 */
body {
  background: $grey-lighter;
  margin: 0;
  padding-top: calc(64px + 24px);
  margin-bottom: 24px;
}

.container {
  max-width: 1000px;
  width: calc(100% - 32px);
  margin: 0 auto;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.actions {
  margin-top: 24px;
  text-align: center;
}

form {
  .form-group{
    &.error {
      label {
        color: $red;
      }

      input {
        border-color: $red;
      }
    }

    &:not(:first-child) {
      margin-top: 16px;
    }
  }

  .form-error {
    background: rgba(lighten(desaturate($red, 30%), 20%), .2);
    border-left: 4px solid $red;
    padding: 12px;
    margin-bottom: 24px;
  }

  label {
    font-weight: 600;
    display: block;

    &.radio-label {
      font-weight: 400;
    }
  }

  input, select {
    margin-top: 4px;
    width: 100%;
    padding: 8px;
  }
  
  input {
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, .1) inset;
    
    &:not([type=checkbox]) {
      border-radius: $border-radius;
      border: 1px solid $grey-light;
      width: calc(100% - 26px);
      font-size: $font-size;
    }

    &[type=radio], &[type=checkbox] {
      display: inline-block;
      width: auto;
    }
  }

  .field-error {
    color: $red;
  }
}

button {
  padding: 12px 18px;
  border-radius: $border-radius;
  font-weight: 600;
  cursor: pointer;
  border: none;

  &.primary {
    background: lighten($red, 20%);
    color: white;

    &:hover {
      background: $red;
    }
  }

  &.secondary {
    background: $grey-light-2;

    &:hover {
      background: $grey-light;
    }
  }

  &:not(:first-child) {
    margin-left: 8px;
  }
}