.auth-page {
  @extend %panel-with-padding;

  margin: 0 auto;
  width: 300px;
  border-top: 4px solid $red;

  .auth-site-title {
    font-family: Merriweather, serif;
    font-weight: bold;
    color: #b61919;
    font-size: 24px;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 8px;

    &::after {
      content: '';
      display: block;
      width: 40px;
      height: 2px;
      background: $red;
      position: relative;
      margin: 0 auto;
      margin-top: 12px;
    }
  }

  .auth-page-title {
    color: darken($grey, 15%);
    text-align: center;
    font-size: 20px;
    margin-bottom: 24px;
  }

  .auth-error {
    background: rgba(lighten(desaturate($red, 30%), 20%), .2);
    border-left: 4px solid $red;
    padding: 12px;
    margin-bottom: 24px;
  }

  input:not([type="checkbox"]) {
    display: inline-block;
    padding: 12px;
    border-radius: $border-radius;
    border: 1px solid $grey-light;
    width: calc(100% - 26px);
    font-size: 15px;
  }

  .primary-button {
    display: block;
    background: lighten($red, 20%);
    color: white;
    width: 100%;
    padding: 12px;
    border: none;
    border-radius: $border-radius;
    cursor: pointer;
    letter-spacing: .05em;
    font-size: 15px;

    &:hover, &:active {
      background: $red;
    }
  }

  .form-group {
    margin: 16px 0;
  }

  .checkbox-label {
    display: block;
    text-align: center;
    font-size: 15px;
  }

  .forgot-password {
    display: block;
    text-align: center;
    margin: 24px -24px -24px;
    color: $grey;
    background: $grey-light-2;
    padding: 20px;

    a {
      font-weight: 700;
      color: darken(grey, 15%);
    }
  }
}